.dashboard {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  margin: 0 auto;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  width: auto !important;
  padding-top: 5px;
  padding-right: 10px;
  padding-left: 10px;
}

.dashboard__mobile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  border-radius: 4px;
  width: 80%;
  padding-top: 5px;
}
